body {
    margin: 0;
    overflow: hidden;
    display: flex;
    align-items: stretch;
    justify-content: stretch;
}

#info {
    position: absolute;
    top: 10px;
    width: 100%;
    text-align: center;
    z-index: 100;
    display: block;
}

canvas,
div {
    margin: 0;
    padding: 0;
    border: none;
}

/* Container for the UI card */
.concert-one-regular {
    font-family: "Concert One", serif;
    font-weight: 400;
    font-style: normal;
}

.ui-card {
    width: 350px;
    background-color: #fff;
    border: 4px solid #2f373f;
    border-radius: 8px;
    color: white;
    font-family: Arial, sans-serif;
    position: absolute;
    top: 60px;
    right: 30px;
    padding: 10px;
    display: none;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.3);
}

/* Header */
.ui-header {
    font-family: "Concert One", serif;
    background-color: #fff;
    color: #465cbb;
    padding: 5px 10px;
    font-weight: bolder;
    font-size: 32px;
    border-radius: 5px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.ui-header::after {
    content: "";
    width: 25px;
    height: 25px;
    background-color: #465cbb;
    border-radius: 50%;
    display: inline-block;
}

/* Content */
.ui-content {
    font-family: "Concert One", serif;
    font-size: 20px;
    text-align: justify;
    color: #fff;
    background-color: #465cbb;
    padding: 10px;
    margin-top: 5px;
    border-radius: 5px;
    height: 320px;
    overflow-y: scroll;
}

/* Custom Scrollbar Styles */
.ui-content::-webkit-scrollbar {
    width: 12px;
}

.ui-content::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 6px;
    margin: 5px;
}

.ui-content::-webkit-scrollbar-thumb {
    background: linear-gradient(to bottom, #666, #444);
    border-radius: 6px;
    border: 2px solid rgba(0, 0, 0, 0.3);
}

.ui-content::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(to bottom, #777, #555);
}

/* Firefox scrollbar styles */
.ui-content {
    scrollbar-width: thin;
    scrollbar-color: #666 rgba(0, 0, 0, 0.3);
}

/* Rewards section */
.ui-button {
    display: flex;
    align-items: center;
    margin-top: 10px;
    background-color: #fff;
    padding: 5px;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
}

.ui-button span {
    font-weight: bold;
}

.ui-button button {
    font-family: "Concert One", serif;
    font-size: 20px;
    color: #fff;
    background-color: #465cbb;
    padding: 5px;
    border-radius: 5px;
    border: 0px;
    width: 100%;
    height: fit-content;
    transition: background-color 0.5s, transform 0.2s ease;
    ;
}

.ui-button button:hover {
    background-color: #6d88ff;
    transform: scale(1.02);
}